.main{
    height: 50vh; //fallback option
    height: calc(var(--vh, 1vh) * 50);
    background: $white;
    .row{
        flex-direction: column;
        justify-content: center;
        h2{
            font-size: 3rem;
            line-height: 4.2rem;
            font-weight: 700;
            z-index: 10;
            color: $white;
            mix-blend-mode: difference;
            @include media("<=tablet"){
                font-size: 2.4rem;
                line-height: 3rem;
            }
            @include media("<=phone"){
                margin-top: 72px;
                font-size: 1.7rem;
                line-height: 2.2rem;
            }
            .line{
                margin-bottom: 8px;
                height: 56px;
                position: relative;
                overflow: hidden;
                @include media("<=tablet"){
                    height: 48px;
                }
                @include media("<=phone"){
                    height: 32px;
                }
                span{
                    position: absolute;
                }
            }
        }
        .btn-row{
            width: 256px;
            position: relative;
            z-index: 2;
            a{
                font-size: 1.6rem;
                color: $black;
                text-decoration: none;
                display: flex;
                align-items: center;
                font-weight: 600;
                @include media("<=tablet"){
                    font-size: 1.3rem;
                }
                @include media("<=phone"){
                    font-size: 1.1rem;
                }
                svg{
                    margin-left: 16px;
                    border: 2px solid $black;
                    padding: 12px;
                    height: 24px;
                    width: 24px;
                    border-radius: 100px;
                    transition: .4s ease-in-out;
                    @include media("<=tablet"){
                        padding: 6;
                        width: 20px;
                        height: 16px;
                    }
                    @include media("<=phone"){
                        padding: 4px;
                        width: 16px;
                        height: 16px;
                    }
                }
                &:hover{
                    //cuando de haga hover en la etiqueta < a > cambiar√° esto de< svg >
                    svg{
                        background: $black;
                        color: $white;
                    }
                }
            }
        }
    }
}