.header{
    position: absolute;
    // position: relative;
    width: 100%;
    height: 128px;
    z-index: 4;
    @include media("<=phone"){
        height: 96px;
    }
    .logo{
        a{
            font-size: 1.2rem;
            letter-spacing: .05rem;
            color: $black;
            text-decoration: none;
            font-weight: 700;
        }
    }
    .nav-toggle{
        cursor: pointer;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        @include media('<=tablet'){
            margin-right: 24px;
        }
        @include media('<=phone'){
            margin-right: 16px;
            width: 20px;
        }
        .hamburger-menu{
            width: 25px;
            span{
                margin-bottom: .3rem;
                background: $black;
                height: 2px;
                width: 100%;
                display: block;
            }
        }
        .hamburger-menu-close{
            position: absolute;
            display: none; //esconder menu para cerrar para contralarlo con react
            svg{
                @include media('<=tablet'){
                    width: 56px;
                }
                @include media('<=phone'){
                    width: 48px;
                }
            }
        }
    }
}